/******** hearsid *********************************************/
body {
  font-family: sans-serif, Courier, monospace;
  margin: 0;
  width: 100%;
  height: 100%;
}

.contacts-container {
  display: flex;
  flex-wrap: wrap;
}

.contact-image {
  border: 1px solid darkgrey;
  border-radius: 3px;
  padding: 2px;
}
.contact-image:hover {
  // animation-name: scale-image;
  // animation-duration: 0.5s;
}

@keyframes scale-image {
  0% {
    transform: scale(1.5, 1.5);
  }
  30% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(-10deg);
  }
  80% {
    border: 5px solid mediumpurple;
    border-radius: 4px;
    display: inherit;
  }
}

/* Add animation */
.my-repeat-animation.ng-enter.ng-enter-active,
.my-repeat-animation.ng-leave {
  opacity: 1;
  -webkit-transition: opacity 300ms linear;
  -moz-transition: opacity 300ms linear;
  transition: opacity 300ms linear;
}

/* Remove animation */
.my-repeat-animation.ng-leave.ng-leave-active,
.my-repeat-animation.ng-enter {
  opacity: 0;
  -webkit-transition: opacity 300ms linear;
  -moz-transition: opacity 300ms linear;
  transition: opacity 300ms linear;
}

.contact {
  text-align: center;
  display: inline-flex;
  border: 1px solid gray;
  border-radius: 5px;
  margin: 4px;
  padding: 4px;
  width: 400px;

  &:hover {
    background: var(--opaque-background);
  }

  .thumbnail {
    float: none !important;
    width: 100%;
    border: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: content;

  }

  
  .media-body {
    width: 100%;
    text-align: left;
    padding: 5px;
    display: flex;
    flex-direction: column;

    .info-container {
      display: grid;
      grid-template-columns: 1fr 1fr;
      line-height: 20px;
      margin-top: auto;
      margin-bottom: auto;
      font-weight: 400;
      font-size: 12px;
      
      .label {
        margin-right: auto;
        
        color: rgba(58, 57, 57, 0.837);
      }

      .value {
        margin-left: auto;
      }
    }

    .media-heading {
      width: 100%;

      a {
        display: inline-block;
        float: right;
        margin-right: 10px;
      }
    }
  }
}
/**************************************************************/
.bs-header {
  position: relative;
  padding: 30px 15px;
  font-size: 20px;
  color: #cdbfe3;
  text-align: center;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  background-color: #6f5499;
  background-image: -webkit-linear-gradient(
    top,
    var(--primary-color) 0%,
    #6f5499 100%
  );
  background-image: linear-gradient(
    to bottom,
    var(--primary-color) 0%,
    #6f5499 100%
  );
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='var(--primary-color)', endColorstr='#6F5499', GradientType=0);
}

.bs-header h1 {
  margin-top: 0;
  color: #fff;
}

.bs-header p {
  margin-bottom: 0;
  font-weight: 300;
  line-height: 1.4;
}

.media-object {
  width: 128px;
  height: 128px;
  border-radius: 5px;
}

.media-list {
  text-align: center;
}

.btn-outline {
  color: var(--primary-color);
  background-color: transparent;
  border-color: var(--primary-color);
}

.btn-lg,
.btn-group-lg > .btn {
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.3333333;
  border-radius: 6px;
}

.btn {
  display: inline-block;
  margin-bottom: 0;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  white-space: nowrap;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  border-radius: 4px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.btn-outline:hover,
.btn-outline:focus,
.btn-outline:active {
  color: #fff;
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

a {
  color: var(--primary-color);
}

a:hover,
a:focus {
  color: #1d1429;
}

a.thumbnail:hover,
a.thumbnail:focus,
a.thumbnail.active {
  border-color: var(--primary-color);
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
  width: 80%;
}

.glyphicon {
  position: relative;
  top: 1px;
  display: inline-block;
  font-family: "Glyphicons Halflings";
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.glyphicon-pencil:before {
  content: "\270f";
}

.gg-trash {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 10px;
  height: 12px;
  border: 2px solid transparent;
  box-shadow: 0 0 0 2px, inset -2px 0 0, inset 2px 0 0;
  border-bottom-left-radius: 1px;
  border-bottom-right-radius: 1px;
  margin-top: 4px;
}

.gg-trash::after,
.gg-trash::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
}

.gg-trash::after {
  background: currentColor;
  border-radius: 3px;
  width: 16px;
  height: 2px;
  top: -4px;
  left: -5px;
}

.gg-trash::before {
  width: 10px;
  height: 4px;
  border: 2px solid;
  border-bottom: transparent;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  top: -7px;
  left: -2px;
}

.gg-trash:hover {
  cursor: pointer;
}
